import React, { useState, useEffect, useRef } from 'react';
import SEO from '@components/SEO';
import Text from '@components/Text';
import Link from '@components/Link';
import Button from '@components/Button';
import Image from '@components/Image';
import Icon from '@components/Icon';
import { classdFn as classNames } from 'classd';
import { useHover } from '@react-aria/interactions'
import pageStyles from '@styles/pages/catalog.module.scss';
import cardStyles from '@styles/components/album-card.module.scss';
// import projects from '../data/projects.json';
import order from '../data/order.json';

export default (props) => {

  const { data } = props;
  const projectKeys = Object.keys(data.projects);

  const styles = {
    ...pageStyles,
    cards: cardStyles
  }

  const [open, setOpen] = useState(false);
  const [width, setWidth] = useState(9999);
  const [height, setHeight] = useState(9999);

  const containerRef = useRef(null);
  const wrapperRef = useRef(null);

  useEffect(()=>{
    let hash = window.location.hash;
    if (hash !== '') {
      let id = hash.split('#')[1];
      if (projectKeys.find(project => project.id === id) !== -1) {
        setOpen(id);
      }
    }

    setWidth(window.innerWidth);
    setHeight(window.innerHeight);

    const handleResize = () => {
      setWidth(window.innerWidth);
      setHeight(window.innerHeight);
    }
    
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
      wrapperRef.current.parentElement.style.overflow = '';
    }
  }, []);

  useEffect(()=>{
    if (width < 992) return;
    if (open && wrapperRef.current) {
      wrapperRef.current.parentElement.style.overflow = 'hidden';
    } else {
      wrapperRef.current.parentElement.style.overflow = 'auto';
    }
  }, [open]);

  let listStyle = {};
  if (width >= 992) {
    // containerRef.current.querySelector(cardStyles.wrapper)
    listStyle = {
      height: `calc(30vh * ${parseFloat((projectKeys.length) + .5)}`
    }
  }
  
  return(
    <div className={styles.wrapper} ref={wrapperRef}>
      <SEO title="Catalog" />
      <div className={styles.main}>
        <Text h1 className={styles.title}>Catalog</Text>
        <Text>Curation &amp; Creative Direction <span className={styles.brmd}>by</span><br className={styles.brm}/> <Link to="https://whateverjewel.world" className={styles.link} external>Jewel Ham<Icon name="linkArrow" /></Link></Text>
        <Text>Design &amp; Code <span className={styles.brmd}>by</span><br className={styles.brm}/> <Link to="https://matthewia.com" className={styles.link} external>Matthew Alicea<Icon name="linkArrow" /></Link></Text>
        <Text>
          Special thanks to the Treble Team<br/>
          Executive assistance by Harmoni Moore,<br/>
          Lesley Pierrot,<br/>
          Makayla Claiborne,<br/>
          McKenzie Grant-Gordon,<br/>
          &amp; all who helped manifest this project
        </Text>
      </div>

      <div className={styles.albums} ref={containerRef}>
        <div className={styles.albumList} style={listStyle}>
          {order.map(id =>
            <Album 
              key={id} 
              data={{ 
                id: id, 
                ...data.projects[id],
                artists: [
                  { 
                    id: data.projects[id].artists[0],
                    ...data.artists[data.projects[id].artists[0]]
                  },
                  { 
                    id: data.projects[id].artists[1],
                    ...data.artists[data.projects[id].artists[1]] 
                  }
                ],
                assets: data.assets,
                name: data.projects[id].work.visual[0].name,
              }}
              open={(open === id)}
              setOpen={setOpen}
              screenW={width}
              screenH={height}
              containerRef={containerRef}
            />
          )}
        </div>
      </div>
    </div>
  );
};

function Album({ data, open, setOpen, screenW, screenH, containerRef }) {
  const styles = cardStyles;
  const placeholderRef = useRef(null);
  const cardRef = useRef(null);
  const [top, setTop] = useState(0);
  // const [width, setWidth] = useState(0);
  const [showCredits, setShowCredits] = useState(false);
  const [cardStyle, setCardStyle] = useState({});
  const [scroll, setScroll] = useState(0);

  const { hoverProps, isHovered } = useHover({});

  let isMobile = false;
  if (screenW < 992) isMobile = true;
  
  const handleOpen = () => {
    setOpen(data.id);
  }

  const handleClose = () => {
    setOpen(false);
  }

  const handleShowCredits = (e, show) => {
    if (e) e.stopPropagation();
    setShowCredits(show);
  }

  const handleCardClick = () => {
    if (!open && containerRef.current) {
      setScroll(containerRef.current.parentElement.parentElement.scrollTop);
    }
    if (!open) handleOpen();
    else if (showCredits) {
      handleShowCredits(null, false);
    }
  }

  const getTopMobile = () => {
    if (isMobile) {
      if (!containerRef.current || !placeholderRef.current) return;
      let pTop = placeholderRef.current.getBoundingClientRect().top;
      let cTop = containerRef.current.getBoundingClientRect().top;
      return pTop - cTop;  
    }
    return false;
  }

  // When screenW width changes
  useEffect(()=>{
    if (placeholderRef.current) {
      if (isMobile) {
        if (!containerRef.current) return;
        let pTop = placeholderRef.current.getBoundingClientRect().top;
        let cTop = containerRef.current.getBoundingClientRect().top;
        setTop(Math.round(pTop - cTop));  
      }

      setTop(Math.round(placeholderRef.current.getBoundingClientRect().top));
    }
  }, [screenW]);

  useEffect(()=>{
    let t = top;
    // if (screenH > 900) t += 48;
    if (isMobile) {
      let tM = getTopMobile();
      if (tM === false) return;
      tM += 64;
      setCardStyle({
        transform: !open 
          ? `translateY(${tM}px) perspective(1200px) rotateX(-28deg)`
          : `translateY(${tM - 48}px) perspective(1200px) rotateX(0)`
      });
    } else {
      if (!containerRef.current) {
        setCardStyle({
          transform: !open 
            ? `translateY(${t}px) perspective(400px) ${isHovered ? 'rotateX(-6deg)' : 'rotateX(-8deg)'}`
            : `translateY(${24}px) perspective(0px) rotateX(0)`
        });
      } else {
        setCardStyle({
          transform: !open 
            ? `translateY(${t}px) perspective(400px) ${isHovered ? 'rotateX(-6deg)' : 'rotateX(-8deg)'}`
            : `translateY(${24 + scroll}px) perspective(0px) rotateX(0)`
        });
      }
    }
  }, [top, open, isHovered, screenW])

  const mainImage = `${data.assets.cdn}/works/${data.artists[0].id}-1.jpg`;
  const backgroundImage = {backgroundImage: `url("${mainImage}")`};
  const backgroundColor = {backgroundColor: data.color.bg};
  // const backgroundColorBtn = {backgroundColor: `${data.color.bg}CC`};

  const getImagePath = (index) => {
    return `${data.assets.cdn}/catalog/${data.artists[index].id}.png`;
    // let path = data.artists[index].img.catalog;
    // if (path && path !== '') {
    //   return path;
    // } else {
      
    // }
  }
  
  const images = [
    getImagePath(0),
    getImagePath(1),
  ]

  return (
    <>
      <div 
        ref={placeholderRef}
        className={styles.placeholder} 
      />
      {top && placeholderRef.current ? 
        <div 
          ref={cardRef}
          className={classNames(
            styles.wrapper, 
            open ? styles.open : null,
            showCredits ? styles.showingCredits : null,
          )}
          onClick={handleCardClick}
          style={cardStyle}
          {...hoverProps}
        >
          <div className={styles.inner} style={false ? backgroundColor : {}}>
            <div className={styles.card}>
              <div className={classNames(styles.peek, styles.face, styles.front)}>
                <div className={styles.bg} style={backgroundImage}>
                </div>
                <div className={styles.fg}>
                  <div className={styles.title}>
                    <div>{`${data.artists[0].name} x`}</div>
                    <div>{`${data.artists[1].name}`}</div>
                  </div>
                </div>
              </div>
              <div className={classNames(styles.main, styles.face, styles.back)} style={backgroundColor}>
                <div className={styles.header}>
                  <div>
                    <Image 
                      className={styles.thumbnail} 
                      src={mainImage} 
                    />
                    <Text h2 className={styles.title}>
                      {/* What if the title is really quite long */}
                      {data.name}
                    </Text>
                  </div>
                  <div 
                    className={styles.close}
                    onClick={handleClose}
                  >
                    <Icon name="x" />
                  </div>
                </div>
                <div className={styles.body}>
                  <div className={classNames(styles.copy, styles.left)}>
                    <img 
                      className={styles.cutout}
                      src={images[0]}
                    />
                    <Text h3 className={styles.heading}>{data.artists[0].name}</Text>
                    {data.artists[0].text.catalog.map((text, i) =>
                      <Text key={i}>
                        {text}
                      </Text>
                    )}
                  </div>
                  <div className={classNames(styles.copy, styles.right)}>
                    <img 
                      className={styles.cutout}
                      // src={'/images/mtn.png'}
                      src={images[1]}
                    />
                    <Text h3 className={styles.heading}>{data.artists[1].name}</Text>
                    {data.artists[1].text.catalog.map((text, i) =>
                      <Text key={i}>
                        {text}
                      </Text>
                    )}
                  </div>
                </div>
                <Button to={`/gallery/${data.id}`} className={styles.galleryLink}>View in Gallery</Button>
              </div>
            </div>
            <div 
              className={classNames(
                styles.credits,
                showCredits ? styles.show : null,
              )}
              onClick={(e)=>{ handleShowCredits(e, true) }}
            >
              <Text h3 className={styles.title}>Credits</Text>
              <ul>
              {data.artists.map((artist, i) =>
                  <li className={styles.artist} key={`${i}-${artist.name}`}>
                    <Text className={styles.artistName}>{artist.name}</Text>
                    <Text className={styles.artistTitle}>{artist.title}</Text>
                    <Link  
                      className={styles.artistLink}
                      to={artist.links[0].url}
                      external
                    >
                      {artist.links[0].label}
                      <Icon name="linkArrow" />
                    </Link>
                  </li>
                )}
                {(data.credits && data.credits.length > 0) && data.credits.map((artist, i) =>
                  <li className={styles.artist} key={`${i}-${artist.name}`}>
                    <Text className={styles.artistName}>{artist.name}</Text>
                    <Text className={styles.artistTitle}>{artist.title}</Text>
                    <Link  
                      className={styles.artistLink}
                      to={artist.link.url}
                      external
                    >
                      {artist.link.label}
                      <Icon name="linkArrow" />
                    </Link>
                  </li>
                )}
              </ul>
            </div>
            <div className={styles.spacer} />
          </div>
        </div>
        : null
      }
    </>
  );
}